export function generateTitleFilterOPtions(text, fontPath, textConfig: any, start, duration) {
  const positionMap = {
    top: '10',
    middle: '(h-text_h)/2',
    bottom: 'h-text_h-10'
  };
  const alignMap = {
    left: '10',
    center: '(w-text_w)/2',
    right: 'w-text_w-10'
  };
  return{
    text: text,
    fontfile: fontPath, // 确保字体文件存在或使用系统字体
    fontsize: textConfig.fontSize || 36,
    fontcolor: textConfig.fontColor || 'white',
    x: alignMap[textConfig.textAlign || 'center'],
    y: positionMap[textConfig.position || 'top'],
    shadowcolor: 'black',
    shadowx: 2,
    shadowy: 2,
    enable: `between(t,${start},${start + duration})`
  }
}

